import * as actionTypes from "../actions/actionTypes"

const initialState = {
	filesTransferList: {},
}

const setFilesTransferList = (state, action) => {
	let newFileList = {
		...state.filesTransferList,
		[action.file.id]: {
			...action.file,
		},
	}

	return {
		...state,
		filesTransferList: newFileList,
	}
}

const clearFilesTransferList = (state) => {
	let newList = {}

	for (const item in state.filesTransferList) {
		if (state.filesTransferList[item].progress < 100) {
			newList = {
				...newList,
				[item]: {
					...state.filesTransferList[item],
				},
			}
		}
	}

	return {
		...state,
		filesTransferList: newList,
	}
}

const reducer = (state = initialState, action) => {
	switch (action.type) {
		case actionTypes.SET_FILES_TRANSFER_LIST:
			return setFilesTransferList(state, action)
		case actionTypes.CLEAR_FILES_TRANSFER_LIST:
			return clearFilesTransferList(state, action)
		default:
			return state
	}
}

export default reducer
