import axios from "../../config/axios"

import * as authAPI from "../api/auth"
import authServerIp from "../../config/auth-server-ip"

export const getUserToken = () => {
  const token = localStorage.getItem("token")

  return token
}

const eraseData = () => {
  localStorage.removeItem("token")
  localStorage.removeItem("expirationDate")
}

export const logout = () => {
  const request = {
    token: getUserToken(),
  }
  axios
    .post(authServerIp + "/logout-hardt", request)
    .then(() => {
      eraseData()
      if (window.location.pathname.split("/")[1] !== "login") {
        window.location = "/login"
      }
    })
    .catch((err) => {
      eraseData()
      if (window.location.pathname.split("/")[1] !== "login") {
        window.location = "/login"
      }
      throw err
    })
}

export const checkAuthTimeout = (time) => {
  setTimeout(() => {
    logout()
  }, time)
}

export const authCheckState = () => {
  const expirationDate = new Date(localStorage.getItem("expirationDate"))
  const token = getUserToken()

  if (!token || expirationDate < new Date()) {
    logout()
    return
  }

  checkAuthTimeout(expirationDate.getTime() - new Date().getTime())
}

export const login = async (actions) => {
  actions.appState.startLoading()

  try {
    await authAPI.login({
      usuario: actions.usuario,
      senha: actions.senha,
      recaptchaToken: actions.recaptchaToken,
    })

    actions.appState.stopLoading()

    return true
  } catch (err) {
    actions.appState.stopLoading()
    actions.appState.setError(err)
  }

  return
}
