import axios from "../../config/axios"
import authServerIp from "../../config/auth-server-ip"
import isGraphQLError from "../functions/isGraphQLError"

export const login = async (dados) => {
  try {
    const request = {
      usuario: dados.usuario,
      senha: dados.senha,
      Captcha: dados.recaptchaToken,
    }

    const res = await axios.post(authServerIp + "/login-hardt", request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    const expirationDate = new Date(new Date().getTime() + 480 * 60 * 1000)
    localStorage.setItem("token", res.data.token)
    localStorage.setItem("expirationDate", expirationDate)

    window.location = "/"
  } catch (err) {
    throw err
  }
}
