import React, { useState } from "react"

import Tooltip from "@material-ui/core/Tooltip"

const EnhancedTooltip = ({ title, children }) => {
  const [open, setOpen] = useState(false)

  return (
    <span
      onMouseEnter={() => {
        setOpen(true)
      }}
      onMouseLeave={() => {
        setOpen(false)
      }}
      onClick={() => {
        setOpen(false)
      }}
    >
      <Tooltip enterDelay={0} enterTouchDelay={0} open={open} title={title}>
        {children}
      </Tooltip>
    </span>
  )
}

export default EnhancedTooltip
