import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import Snackbar from '@material-ui/core/Snackbar'
import MuiAlert from '@material-ui/lab/Alert'

const Alert = ({
	open,
	type,
	message,
}) => {
	const [alertOpen, setAlertOpen] = useState(false)

	const handleClose = (event, reason) => {
		if (!reason === 'clickaway') {
			setAlertOpen(false)
		}
	}

	useEffect(() => {
		open ? setAlertOpen(true) : setAlertOpen(false)
	}, [open])

	return (
		<Snackbar
			autoHideDuration={type && type === "error" ? 4000 : 2000}
			onClose={handleClose}
			open={alertOpen}
		>
			<MuiAlert
				severity={type && type}
				variant="filled"
			>
				{message}
			</MuiAlert>
		</Snackbar>
	)
}

Alert.propTypes = {
	open: PropTypes.bool,
	message: PropTypes.string,
	type: PropTypes.string,
}

export default Alert