import React, { Suspense, useState, useEffect } from "react"
import { Redirect, Route, Switch, useHistory } from "react-router-dom"

import getErrorMessage from "./functions/getErrorMessage"
import { authCheckState } from "./services/functions/auth"
import { createMuiTheme } from "@material-ui/core/styles"
import { ThemeProvider } from "@material-ui/styles"
import Spinner from "./components/UI/Spinner"
import Layout from "./components/Layout"
import Alert from "./components/UI/Alert"
import FileTransferList from "./components/fileTransferList"
import estilos from "./app.module.css"

const Home = React.lazy(() => import("./containers/Home"))
const Administradores = React.lazy(() => import("./containers/Administradores"))
const Arquivos = React.lazy(() => import("./containers/Arquivos"))
const Clientes = React.lazy(() => import("./containers/Clientes"))
const Denominacoes = React.lazy(() => import("./containers/Denominacoes"))
const Login = React.lazy(() => import("./containers/Login"))
const Logout = React.lazy(() => import("./containers/Logout"))
const Sessoes = React.lazy(() => import("./containers/Sessoes"))
const Usuarios = React.lazy(() => import("./containers/Usuarios"))

const theme = createMuiTheme({
	palette: {
		primary: {
			main: "#013E55",
		},
	},
})

const App = (props) => {
	const history = useHistory()
	const [loading, setLoading] = useState(false)
	const [isAuthenticated, setIsAuthenticated] = useState(false)
	const [error, setError] = useState(null)
	const [showingError, setShowingError] = useState(false)
	const [errorMessage, setErrorMessage] = useState(null)
	const [showingSuccess, setShowingSuccess] = useState(false)

	const startLoading = () => {
		setLoading(true)
	}

	const stopLoading = () => {
		setLoading(false)
	}

	useEffect(() => {
		const expDate = localStorage.getItem("expirationDate")

		if (expDate) {
			setIsAuthenticated(true)
			authCheckState()
		}
	}, [])

	const errorHandler = (err) => {
		const errorMessage = getErrorMessage(err)

		setErrorMessage(errorMessage)

		console.error(err)
		console.log({ err })

		setShowingError(true)
		setTimeout(() => {
			setShowingError(false)
		}, 2000)
	}

	const successHandler = () => {
		setShowingSuccess(true)
		setTimeout(() => {
			setShowingSuccess(false)
		}, 2000)
	}

	useEffect(() => {
		if (error) {
			errorHandler(error)
		}
	}, [error])

	const appState = {
		loading,
		isAuthenticated,
		startLoading,
		stopLoading,
		setError,
		setSuccess: successHandler,
	}

	let routes = (
		<Switch history={history}>
			<Route
				path="/"
				exact
				render={(props) => <Login appState={appState} {...props} />}
			/>

			<Redirect to="/" />
		</Switch>
	)

	if (isAuthenticated) {
		routes = (
			<Switch history={history}>
				<Route path="/" exact render={(props) => <Home {...props} />} />
				<Route
					path="/administradores"
					render={(props) => <Administradores appState={appState} {...props} />}
				/>
				<Route
					path="/arquivos"
					render={(props) => <Arquivos appState={appState} {...props} />}
				/>
				<Route
					path="/clientes"
					render={(props) => <Clientes appState={appState} {...props} />}
				/>
				<Route
					path="/denominacoes"
					render={(props) => <Denominacoes appState={appState} {...props} />}
				/>
				<Route
					path="/sessoes"
					render={(props) => <Sessoes appState={appState} {...props} />}
				/>
				<Route
					path="/usuarios"
					render={(props) => <Usuarios appState={appState} {...props} />}
				/>
				<Route path="/sair" render={(props) => <Logout {...props} />} />

				<Redirect to="/" />
			</Switch>
		)
	}

	return (
		<React.Fragment>
			<ThemeProvider theme={theme}>
				{isAuthenticated ? (
					<Layout appState={appState} className={estilos}>
						<Suspense fallback={<Spinner />}>{routes}</Suspense>
					</Layout>
				) : (
					<Suspense fallback={<Spinner />}>{routes}</Suspense>
				)}

				<Alert open={showingError} message={errorMessage} type="error" />
				<Alert open={showingSuccess} message={"Salvo!"} type="success" />

				<FileTransferList />
			</ThemeProvider>
		</React.Fragment>
	)
}

export default App
