import React, { useState } from "react"
import { connect } from "react-redux"

import * as actions from "../../store/actions/index"
import IconButton from "@material-ui/core/IconButton"
import {
  ChevronLeft,
  ChevronRight,
  CheckCircle,
  ClearAll,
  Description,
  Error,
} from "@material-ui/icons/"
import Tooltip from "../UI/Tooltip/Tooltip"
import estilos from "./estilos.module.css"

const FileTransferProgress = (props) => {
  const [isListOpen, setIsListOpen] = useState(true)

  const { files, limparLista } = props

  let list = []

  if (files) {
    for (const item in files) {
      list.push(
        <div className={estilos.ListItem} key={item + Math.random() * 10000}>
          <Description className={estilos.DocumentIcon} fontSize="small" />
          <span title={files[item].filename} className={estilos.TituloArquivo}>
            {files[item].filename.slice(0, 16) + "..."}
          </span>
          {files[item].progress === 100 ? (
            <div title="Download concluído">
              <CheckCircle
                className={estilos.DownloadProgressIconSuccess}
                fontSize="small"
              />
            </div>
          ) : files[item].progress !== "error" ? (
            files[item].progress + "%"
          ) : (
            <div title="Ocorreu um erro. Tente novamente!">
              <Error
                className={estilos.DownloadProgressIconError}
                fontSize="small"
              />
            </div>
          )}
        </div>
      )
    }
  }

  return (
    <React.Fragment>
      <div
        className={estilos.Container}
        style={{
          borderRadius: !isListOpen ? "4px 4px 0 0" : "0px",
          display: !Object.keys(files).length ? "none" : "unset",
        }}
      >
        <div className={estilos.Header}>
          <div className={estilos.Text}>Transferência de arquivos</div>

          <div className={estilos.Icons}>
            <Tooltip enterDelay={0} enterTouchDelay={0} title={"Limpar lista"}>
              <IconButton
                aria-label={"Limpar lista"}
                color="inherit"
                onClick={() => limparLista()}
              >
                <ClearAll />
              </IconButton>
            </Tooltip>
            <Tooltip enterDelay={0} enterTouchDelay={0} title={"Exibir lista"}>
              <IconButton
                aria-label={"Expandir"}
                color="inherit"
                onClick={() => setIsListOpen(!isListOpen)}
              >
                {isListOpen ? <ChevronRight /> : <ChevronLeft />}
              </IconButton>
            </Tooltip>
          </div>
        </div>

        <div
          className={estilos.List}
          style={{
            transform: !isListOpen ? "translateX(100vw)" : "translateX(0)",
          }}
        >
          {list}
        </div>
      </div>
    </React.Fragment>
  )
}

const mapStateToProps = (state) => {
  return {
    files: state.arquivos.filesTransferList,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    limparLista: () => dispatch(actions.clearFilesTransferList()),
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FileTransferProgress)
