const getErrorMessage = (err) => {
	if (err.response) {
		if (err.response.data) {
			return err.response.data.message
		}
	} else if (err.message) {
		return err.message
	} else {
		console.error(err)
		console.log({ err })

		return "Não foi possível se conectar ao servidor"
	}
}

export default getErrorMessage
