import React, { useState } from "react"
import { NavLink } from 'react-router-dom'

import {
	Divider,
	ListItem,
	ListItemText,
	ListItemIcon
} from "@material-ui/core/"
import classes from "./estilos.module.css"
// import HardtLogo from "../../assets/imgs/Logo-Hardt-310x310.png"
import HardtLogo from "../../assets/imgs/Logo-Hardt-303-Branco-Azul.png"

const Sidebar = (props) => {
	const [selected, setSelected] = useState(1)

	let list = (<div></div>)
	list = props.list.map((items, index) => {
		if (items.type === 'divider') {
			return <Divider key={index} />
		}

		return (
			<div>
				{items.map((op, i) => {
					return (
						<NavLink
							className={classes.Link}
							exact={op.exact}
							key={op.label}
							onClick={() => setSelected(index)}
							to={op.to}
						>
							<ListItem
								button
								className={classes.LinkButton}
								selected={selected === index}
							>
								<ListItemIcon>{op.icon}</ListItemIcon>
								<ListItemText primary={op.label} />
							</ListItem>
						</NavLink>
					)
				})}
			</div>
		)
	})

	return (
		<div className={classes.Sidebar}>
			<div className={classes.Header}>
				<img src={HardtLogo} alt="Hardt Informatica Logo" />
				<div>HARDT Admin</div>
			</div>

			{list}
		</div>
	)
}

export default Sidebar
