import React from "react"
import ErrorBoundary from "../ErrorBoundary"

import Sidebar from "../Sidebar"
import LinearProgress from "../UI/LinearProgress"
import estilos from "./estilos.module.css"

import {
	Home,
	RecentActors,
	Folder,
	MeetingRoom,
	AssignmentInd,
	CollectionsBookmark,
	PeopleAlt,
	SettingsInputComponent,
} from "@material-ui/icons"

const Layout = (props) => {
	const { appState } = props

	const drawerList = [
		{ type: "divider" },
		[
			{
				type: "link",
				to: "/",
				label: "Início",
				icon: <Home />,
			},
		],
		[
			{
				type: "link",
				to: "/clientes",
				label: "Clientes",
				icon: <AssignmentInd />,
			},
		],
		[
			{
				type: "link",
				to: "/usuarios",
				label: "Usuários",
				icon: <PeopleAlt />,
			},
		],
		[
			{
				type: "link",
				to: "/arquivos",
				label: "Arquivos",
				icon: <Folder />,
			},
		],
		[
			{
				type: "link",
				to: "/denominacoes",
				label: "Denominações",
				icon: <CollectionsBookmark />,
			},
		],
		[
			{
				type: "link",
				to: "/administradores",
				label: "Administradores",
				icon: <RecentActors />,
			},
		],
		[
			{
				type: "link",
				to: "/sessoes/hardt",
				label: "Sessões",
				icon: <SettingsInputComponent />,
			},
		],
		[
			{
				type: "link",
				to: "/sair",
				label: "Sair",
				icon: <MeetingRoom />,
			},
		],
	]

	return (
		<ErrorBoundary>
			<LinearProgress loading={appState.loading} />

			<div className={estilos.Grid}>
				<Sidebar list={drawerList} />

				<main className={estilos.Layout}>{props.children}</main>
			</div>
		</ErrorBoundary>
	)
}

export default Layout
